import Vue from 'vue'
import Router from 'vue-router'
import Chat from '@/components/chat'
import Disclaimer from '@/components/disclaimer'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Disclaimer
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat
    }
  ]
})



