<template>
    <div class="d_container">
        <div class="containter__content">
            <h1 class="heading">
                Welcome to the Covid-19 Self-checker app
            </h1>
            <div class="checker">
                <p class="checker__content">
                    The divurpose of the Coronavirus Self-Checker is to help you make decisions about seeking appropriate
                    medical care. This system is not intended for the diagnosis or treatment of disease, including COVID-19.
                    This project was made possible through a partnership with the CDC Foundation and is enabled by
                    Microsoft’s Azure platform. CDC’s collaboration with a non-federal organization does not imply an
                    endorsement of any one particular service, product, or enterprise. 
                </p>
                <div class="checker__radio">
                    <span class="radio radio__accept">
                        <input type="radio" name="accept" id="accept" value="yes">
                        <label for="accept">I agree</label>
                    </span>
                    <span class="radio radio__reject">
                        <input type="radio" name="accept" id="reject" value="no">
                        <label for="reject">I disagree</label>
                    </span>
                </div>
            </div>
            <a class="btn" @click="next">Next</a>
            <ul class="list">
                <li class="f"></li>
                <li></li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: 'disclaimer',
    data() {
        return{
            
        }
    },
    methods: {
        next() {
            console.log('hey');
            let errorMsg;
            const accept = document.querySelector('#accept:checked'),
                reject = document.querySelector('#reject:checked');

                if(accept) {
                    this.$router.push({name: 'Chat'});
                }else if(reject){
                    this.$router.go();
                }else{
                    // get parent element
                    const parent = document.querySelector('.containter__content'),
                        childBtn = document.querySelector('.btn');

                         errorMsg = document.querySelector('.error');

                        if(!errorMsg){
                            // create content
                            const p = document.createElement('p');
                                p.classList.add('error');
                                p.appendChild(document.createTextNode('Please check button to proceed'));
        
                            parent.insertBefore(p,childBtn);
        
                            setTimeout(() => {
                                document.querySelector('.error').style.display = 'none';
                                this.$router.go();
                            },3000);
                        }
                }
        }
    },
    mounted() {
        document.querySelector('.list li:first-child').style.opacity = .5;
    }
}
</script>

<style>
    *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  body{
    background-color: #e3e3e3;
    font-family: Arial, Helvetica, sans-serif;
    overflow: hidden;
  }

  .d_container{
    max-width: 768px;
    background: #eee;
    margin: 50px auto;
    height: 80vh;
    box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
    border-radius: 5px;
    /* overflow: hidden; */
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: justify;
    transform: translateY(-.8rem);
  }

  .containter__content{
      height: 80%;
      width: 50vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
  }

  .heading{
      text-align: center;
      font-weight: 100;
      font-size: 2rem;
  }

  .checker__content{
      line-height: 2rem;
      margin-bottom: .5rem;
  }

  .list{
      display: flex;
      justify-content: center;
  }

  .list li{
      list-style: none;
  }

  .list li::before{
      content: "\25CF";
      color:#333;
      margin-right: 2rem;
  }

  .btn{
      align-self:flex-end;
      text-decoration: none;
      background: #fff;
      padding: 1rem;
      color: #333;
      transition: all .5s;
  }

  .btn:hover{
      background: #333;
      color: #fff;
  }

  .radio {
      display: inline-block;
  }

  .radio__accept{
      margin-right: 2rem;
  }

  .error{
      color: white;
      background: red;
      width: 100%;
      margin: .5rem 0;
      text-align: center;
      position: relative;
      display: block;
      z-index: 100;
      padding: .5rem;
  }
</style>


