<template>
  <div class="main-container">
    <div class="chat__content"></div>
  </div>
</template>

<script>
import Questions from '../questions';

export default {
  name: "Chat",
  data () {
    return {
      text: null,
      question: ["hello i am kojo","have you eaten","is your head aching"],
      currentNum: 0,
      totalQuestions: 0,
      introQues: []
    }
  },
  methods:{
    accept(){
      console.log("accepted");
    },
    sendText() {
      if (this.text != null){
        // create element for the questions generated
        const parent = document.querySelector('.chat__content');
        const div = document.createElement('div');
              div.className = "content content-right";
              div.innerHTML=`
                <span class="user-text">${this.text}</span>
                <span class="user">you</span>
              `;

        if(parent.childNodes){
          parent.appendChild(div,parent.lastElementChild);
        }
      
        // call the getQuestion method
        this.getQuestions(this.currentNum);
        this.currentNum++;

        // clear text from input
        this.text = undefined;
      }else{
        console.log('please type a text');
      }
    },
    checkInput() {
      console.log('hoole')
      // if(this.text.length == 0){
      //    return false;
      // }
    },
    getQuestions(num) {
      // create element for text from the user input 
      const parent = document.querySelector('.chat__content')
      const ques = this.introQues[num];
      const div = document.createElement('div');
            div.className = "content content-left";
            div.innerHTML = `
              <span class="bot">Kojo</span>
              <span class="bot-box">${ques}</span>
            `;

      if(parent.childNodes){
        parent.appendChild(div,parent.lastElementChild);
      }
    }
  },
  async created() {
    const questions = await Questions.intro('/api/v1/introquestions');
    this.introQues = await questions.question.map(ques => ques.question);
  },
  computed: {
    // getIntroQuestion(){
    //   this.totalQuestions = this.question.length;

    //   if(this.currentNum <= this.totalQuestions){
    //     this.getQuestions(this.currentNum);
    //     this.currentNum++;
    //   }

    //   console.log(computed);
    // }
  },
  mounted() {
    let botBox,
      botText;

    // CREATING THE DECLAIMER ELEMENT AND INSERTING INTO THE DOM
    const parent = document.querySelector('.chat__content')
      const div = document.createElement('div');
            div.className = "content content-left";
            div.innerHTML = `
              <span class="bot">Kojo</span>
              <span class="bot-box"> 
              <div class="bot-text">
              The divurpose of the Coronavirus Self-Checker is to help you make decisions about seeking appropriate medical care. This system is not intended for the diagnosis or treatment of disease, including COVID-19. This project was made possible through a partnership with the CDC Foundation and is enabled by Microsoft’s Azure platform. CDC’s collaboration with a non-federal organization does not imply an endorsement of any one particular service, product, or enterprise.
              </div>
              </span>
            `;

      if(parent.childNodes){
        parent.appendChild(div,parent);
        botBox = document.querySelector('.bot-box');
        botText = document.querySelector('.bot-text');
      }

      // INSERTING BUTTON INTO THE DOM
      const innerBtn = `
        <div class="checker">
              <a class="btn__accept">
                  I agree
              </a>
              <a class="btn__reject">
                  I don't agree
              </a>
        </div>
      `
      const outerBtn = document.createElement('div');
        outerBtn.className = "checker__radio";
        outerBtn.innerHTML = innerBtn;
      
      botBox.appendChild(outerBtn);
  }
}
</script>

<style>
  *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  body{
    background-color: #e3e3e3;
    font-family: Arial, Helvetica, sans-serif;
    overflow: hidden;
  }

  .main-container{
    max-width: 768px;
    background: #eee;
    margin: 50px auto;
    height: 80vh;
    box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
    border-radius: 5px;
    transform: translateY(-.8rem);
    overflow-y: scroll;
  }

  .chat__content{
    font-weight: 100;
    font-size: .9rem;
    line-height: 1.2rem;
    height: 70vh;
  }

  .content:not(:last-child),
  .content:first-child{
    margin-top: .5rem;
  }

  .content-left{
    margin-left: .5rem;
    cursor: pointer;
    transition: all .5s;
    display: flex;
  }

  .content-left .bot{
    background: darkblue;
    display: inline-block;
    padding: 1rem;
    color: #fff;
    border-radius: 100%;
    margin-right: .5rem;
    text-transform: uppercase;
    align-self: flex-start;
  }

  .content-left .bot-box{
    background: #fff;
    box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
    display: inline-block;
    padding: 1rem;
    border-radius: .5rem;
    max-width: 40%;
  }

  .content-left:hover{
    transform: scale(0.9);
  }

  .content-right{
    display: flex;
    justify-content: flex-end;
    transition: all .5s;
    text-align: end;
  }

  .content-right .user{
    background: darkblue;
    padding: 1rem;
    color: #fff;
    border-radius: 100%;
    margin: 0 .5rem;
    text-transform: uppercase;
    display: inline-block;
    align-self: flex-start;
  }

  .content-right .user-text{
    background: #fff;
    box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
    display: inline-block;
    padding: 1rem;
    border-radius: .5rem;
    max-width: 40%;
  }

  .content-right:hover{
    transform: scale(0.9);
  }

  .checker__radio{
    display: block;
  }

  .btn__accept,
  .btn__reject{
    background: #333;
    color: #fff;
    padding: .5rem;
    display: flex;
    margin-top: 1rem;
    justify-content: center;
    transition: all .5s;
  }

  .btn__accept:hover,
  .btn__reject:hover{
    background: #fff;
    color: #333;
  }
</style>


