<template>
    <div class="navigation">
        <router-link :to="{name: 'Home'}" class="nav-link">covid-19 Self Checker App</router-link>
    </div>
</template>

<script>
export default {
    name: "Navigation"
}
</script>

<style>
    .navigation{
        text-transform: uppercase;
        text-align: center;
        display: inline-block;
        width: 100%;
        padding: .5rem;
        background: darkblue;
        position: sticky;
        top: 0;
    }

    .nav-link{
        color: #fff;
        text-decoration: none;
        font-size: 3rem;
    }
</style>


